@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap');
@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  * {
    box-sizing: border-box;
    cursor: default;
  }
  body {
    @apply bg-gray-50 text-gray-900 font-sans;
    cursor: default;
  }
  button {
    cursor: pointer;
  }
  input, textarea, select {
    cursor: text;
  }
  a {
    cursor: pointer;
  }
  ::-webkit-scrollbar { width: 6px; }
  ::-webkit-scrollbar-track { @apply bg-gray-100; }
  ::-webkit-scrollbar-thumb { @apply bg-gray-300 rounded-full; }
}

@layer components {
  .btn-primary {
    @apply bg-indigo-600 hover:bg-indigo-700 text-white font-medium py-2 px-4 rounded-lg transition-colors duration-200 disabled:opacity-50 disabled:cursor-not-allowed;
    cursor: pointer;
  }
  .btn-secondary {
    @apply bg-white hover:bg-gray-50 text-gray-700 font-medium py-2 px-4 rounded-lg border border-gray-300 transition-colors duration-200;
    cursor: pointer;
  }
  .btn-danger {
    @apply bg-red-500 hover:bg-red-600 text-white font-medium py-2 px-4 rounded-lg transition-colors duration-200;
    cursor: pointer;
  }
  .input-field {
    @apply w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-transparent transition-all text-sm;
    cursor: text;
  }
  .card {
    @apply bg-white rounded-xl shadow-sm border border-gray-100 p-6;
    cursor: default;
  }
  .badge-admin {
    @apply bg-indigo-100 text-indigo-700 text-xs font-medium px-2 py-0.5 rounded-full;
  }
  .badge-member {
    @apply bg-gray-100 text-gray-600 text-xs font-medium px-2 py-0.5 rounded-full;
  }
}

/* Global override â€” fixes any remaining cursor issues */
html, body, div, span, p, li,
h1, h2, h3, h4, h5, h6,
table, thead, tbody, tr, td, th {
  cursor: default !important;
}

button,
a,
[role="button"],
[type="button"],
[type="submit"] {
  cursor: pointer !important;
}

input,
textarea,
select {
  cursor: text !important;
}

input[type="checkbox"],
input[type="radio"] {
  cursor: pointer !important;
}
*:not(input):not(textarea):not(select):not(button):not(a) {
  cursor: default !important;
}

/* Prevent text selection cursor on non-text elements */
.card, .card *, 
nav, nav *,
table, td, th {
  user-select: none;
  -webkit-user-select: none;
}

/* Allow selection only in inputs */
input, textarea {
  user-select: text !important;
  -webkit-user-select: text !important;
}